.watch {
  width: 100%;
  background-color: var(--main-color);
  color: #fff;
  overflow: hidden;

  .back {
    display: flex;
    align-items: center;
    position: absolute;
    top: 10px;
    left: 10px;
    color: var(--white-color);
    cursor: pointer;
    z-index: 2;

    &:hover{
      color: var(--blue-color-light);
      background-color: var(--gray-color);
      border: none;
      border-radius: 15px;
      display: flex;
      align-items: center;
      justify-content: center;
      padding: 2px 4px;
    }
  }

  .video {
    width: 100%;
    margin: 50px 0;
  }
  h3 {
    font-weight: 400;
    
  }
  p {
    line-height: 25px;
    margin-top: 20px;
  }
  .soical {
    display: flex;
    justify-content: left;
    align-items: center;
    margin: 30px 0;
    border-top: 2px solid #575757;
    border-bottom: 1px solid #575757;
    padding: 8px 0;
  }
  .soical img {
    margin-left: 10px;
  }
}
